.compare table {
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 13px 21px -5px rgba(0, 0, 0, 0.05);
  border: 1px solid #eee;
  font-size: 18px;
  table-layout: fixed;
}
.compare table .bg-red {
  background-color: red;
}
.compare table .bg-green {
  background-color: green;
}
.compare table .bg-blue {
  background-color: blue;
}
.compare table tr > td, .compare table tr > th {
  padding-top: 25px;
  padding-bottom: 25px;
  text-align: center;
}
.compare table th[scope="row"] {
  font-size: 20px;
  color: #393c45;
  font-weight: normal;
  background-color: #f9f9f9;
  border: 1px solid #eee;
  text-align: right;
  padding-left: 45px;
  padding-right: 45px;
}
.compare table tr.condition {
  color: #fff;
  font-size: 20px;
}
.compare table tr.colors span {
  height: 20px;
  width: 20px;
  display: inline-block;
  margin-right: 5px;
  border-radius: 100%;
}
.compare table thead th {
  font-size: 20px;
  color: #393c45;
  font-weight: normal;
}
.compare .thead-default th {
  background-color: #fff;
}

